'use client';

import { useState, useRef, useEffect } from 'react';
import * as Switch from '@radix-ui/react-switch';
import styles from './SanctuaryToggleRadix.module.css';

export default function SanctuaryToggleRadix({ sanctuary, onSanctuaryChange }) {
  const [isHolding, setIsHolding] = useState(false);
  const holdTimeoutRef = useRef(null);
  const holdStartTimeRef = useRef(null);

  // Long-press logic: 1.5 seconds to toggle
  const handleMouseDown = () => {
    holdStartTimeRef.current = Date.now();
    setIsHolding(true);
    
    holdTimeoutRef.current = setTimeout(() => {
      onSanctuaryChange(!sanctuary);
    }, 1500);
  };

  const handleMouseUp = () => {
    if (holdTimeoutRef.current) {
      clearTimeout(holdTimeoutRef.current);
    }
    setIsHolding(false);
  };

  const handleTouchStart = () => {
    handleMouseDown();
  };

  const handleTouchEnd = () => {
    handleMouseUp();
  };

  useEffect(() => {
    return () => {
      if (holdTimeoutRef.current) {
        clearTimeout(holdTimeoutRef.current);
      }
    };
  }, []);

  return (
    <div className={styles.container}>
      <div className={styles.toggleWrapper}>
        <div className={styles.label}>
          <span className={styles.icon}>‚ö°</span>
          <div>
            <div className={styles.title}>Focus Switch</div>
            <div className={styles.subtitle}>Hold 1.5s to toggle</div>
          </div>
        </div>
        
        <Switch.Root
          className={`${styles.switchRoot} ${sanctuary ? styles.active : ''}`}
          checked={sanctuary}
          onCheckedChange={onSanctuaryChange}
          disabled={false}
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
          onTouchStart={handleTouchStart}
          onTouchEnd={handleTouchEnd}
        >
          <Switch.Thumb className={styles.switchThumb} />
        </Switch.Root>
      </div>

      {isHolding && (
        <div className={styles.holdIndicator}>
          <div className={styles.holdBar}></div>
          <span className={styles.holdText}>Hold to toggle...</span>
        </div>
      )}

      {sanctuary && (
        <div className={styles.statusBadge}>
          üõ°Ô∏è SANCTUARY MODE ACTIVE
        </div>
      )}
    </div>
  );
}
